<template>
  <div class="poems-list w3-container slide" ref="slide" :style="{ 'height': elemHeight + 'px'}">
    <div class="row">
      <Spinner v-if="initLoading" />
      <div v-for="poem of poems" :key="poem.id" class="poem-container w3-border-bottom w3-padding-16 col-xs-12 col-sm-12 col-md-6 col-lg-4" :class="{selected: poem === selectedPoem}" @click="toggleSelect(poem)">
        <PoemItem :poem="poem" :selectedPoemId="selectedPoem ? selectedPoem.id : -1" storageAction="store"></PoemItem>
      </div>
      <Spinner v-if="poemsLoading && !poemsCompleted" />
    </div>
  </div>
</template>

<script>
import _ from 'lodash'

import { backendService } from '../services/backend-service'
import PoemItem from './PoemItem'
import Spinner from './Spinner'

export default {
  components: {
    PoemItem,
    Spinner
  },

  data () {
    return {
      selectedPoem: null,
      poemsLoading: false,
      poemsCompleted: false
    }
  },

  computed: {
    poems () {
      return this.$store.state.poems
    },
    throttledCheckIfNeedsLoading () {
      return _.throttle(this.checkIfNeedsLoading, 250)
    },
    initLoading () {
      return this.$store.state.initLoading
    },
    elemHeight () {
      return window.innerHeight - this.$store.state.headerHeight - this.$store.state.footerHeight
    }
  },

  mounted () {
    this.poemsCompleted = false
    this.$refs.slide.addEventListener('scroll', this.throttledCheckIfNeedsLoading)
  },

  methods: {
    toggleSelect (poem) {
      this.selectedPoem === poem ? this.selectedPoem = null : this.selectedPoem = poem
    },
    elementClientHeight () {
      return this.$el.clientHeight
    },
    checkIfNeedsLoading () {
      if (this.poemsLoading || this.poemsCompleted) {
        return
      }
      let pixelsFromSlideBottomToListBottom = 0 + this.$refs.slide.scrollHeight - this.elementClientHeight() - this.$refs.slide.scrollTop
      if (pixelsFromSlideBottomToListBottom < 200) {
        this.poemsLoading = true
        backendService.getPoems()
          .then(() => {
            this.$nextTick(() => {
              this.poemsLoading = false
            })
            this.poemsCompleted = this.$store.state.poemsLoadingCompleted
          })
          .catch(err => console.log(err)) // TODO: proper error handling //////////////
      }
    }
  }
}
</script>

<style scoped>
.slide {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow-y: scroll;
}
</style>



